    height: 55px;
    display: -ms-flexbox;
    display: flex;
    width: 1347px;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.headerItemContainer{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}

.headerItem{
    margin: 5px;
}

.customButton{
    composes: customButton from '../../style/global.css';
}


